.room-wrapper {
  width: 100%;
  font-size: 0;
}
.room-wrapper .room-title {
  display: block;
  position: relative;
  height: 30px;
  line-height: 30px;
  font-size: 18px;
  padding-left: 10px;
  color: #000;
}
.room-wrapper .room-title .more {
  position: absolute;
  right: 10px;
  font-size: 12px;
}
.room-wrapper .room-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
}
.room-wrapper .room-list .room-item {
  width: 50%;
  text-align: center;
  padding: 5px;
  box-sizing: border-box;
}
.room-wrapper .room-list .room-item .room-bg {
  position: relative;
  width: 100%;
}
.room-wrapper .room-list .room-item .room-bg .room-image {
  width: 100%;
  height: 100%;
  border-radius: 5%;
}
.room-wrapper .room-list .room-item .room-bg .room-online,
.room-wrapper .room-list .room-item .room-bg .room-nickname {
  position: absolute;
  line-height: 16px;
  font-size: 10px;
  padding: 0 5px;
  color: #fff;
  background: rgba(4,5,5,0.38);
}
.room-wrapper .room-list .room-item .room-bg .room-online {
  right: 0px;
  top: 0px;
}
.room-wrapper .room-list .room-item .room-bg .room-nickname {
  left: 0px;
  bottom: 0px;
}
.room-wrapper .room-list .room-item .room-name {
  font-size: 12px;
  color: #000;
  text-align: left;
  width: 90%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 20px;
  line-height: 20px;
}


